<template lang='pug'>
.operating__table
  .row.head__row
    .elem.title {{ title }}
    .elem(data-number="1") 01
    .elem(data-number="2") 02
    .elem(data-number="3") 03
    .elem(data-number="4") 04
    .elem(data-number="5") 05
    .elem(data-number="6") 06
    .elem(data-number="7") 07
    .elem(data-number="8") 08
    .elem(data-number="9") 09
    .elem(data-number="10") 10
    .elem(data-number="11") 11
    .elem(data-number="12") 12
  .row.event(
    v-for="item in events"
  )
    .elem.name {{ item.name }}
    .elem(
      v-for="item in item.months"
      :data-number="item.num"
    )
      .rotate(
        v-if="item.active == true"
        )
        .element
        .element
</template>

<script>
export default {
  name: "OperatingTable",
  props: ["title", "events"],
  methods: {
    hovers() {
      const elems = this.$el.querySelectorAll(".elem");
      for (let i = 0; i < elems.length; i++) {
        elems[i].addEventListener("mouseenter", () => {
          for (let j = 0; j < elems.length; j++) {
            if (elems[i].dataset.number == elems[j].dataset.number) {
              elems[j].classList.add("active");
            }
          }
        });
        elems[i].addEventListener("mouseleave", () => {
          for (let j = 0; j < elems.length; j++) {
            if (elems[i].dataset.number == elems[j].dataset.number) {
              elems[j].classList.remove("active");
            }
          }
        });
      }
    }
  },
  mounted() {
    this.hovers();
  }
};
</script>

<style lang="sass" scoped>
@import "src/sass/main"
.operating__table
  width: 100%
  font-family: 'Futura PT'
  display: flex
  flex-wrap: wrap
  font-size: 14px
  .row
    display: flex
    transition-duration: .6s
    transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1)
    transform: translateY(90px)
    opacity: 0
    width: 100%
    border-bottom: 1px solid $gold2
    .elem
      height: 90px
      //width: 90px
      +center-flex
      position: relative
      transition-duration: .5s
      display: flex
      width: calc(75% / 12)
      @media (max-width: $l-desktop)
        width: calc(70% / 12)
        height: 60px
      @media (max-width: $m-desktop)
        height: 50px
      &:first-child
        width: 25%
        justify-content: flex-start
        @media (max-width: $l-desktop)
          width: 30%
        @media (max-width: $m-desktop)
          width: 35%
    
  .head__row
    border-bottom: 2px solid #564E43
    .elem
      background-color: transparent
    .title
      font-size: 24px
      color: $grey2!important
      background-color: transparent!important
  .event
    .name
      opacity: .6
      color: $grey2!important
      background-color: transparent!important
      border-right: 1px solid $gold2
.rotate
  display: flex
  width: 8px
  justify-content: space-between
  height: 20px
  .element
    width: 25%
    height: 100%
    transform: rotateY(90deg)
    animation: rotateAnimation 2s linear infinite
    &:last-child
      animation-delay: .2s
.elem.active
  background-color: $beige
  color: $gold
@keyframes rotateAnimation
    0%
      transform: rotateY(90deg)
      background-color: #847053
    50%
      background-color: #FFE9A6
      opacity: 1
    100%
      transform: rotateY(270deg)
      background-color: #847053
</style>
