<template lang='pug'>
.base
  CursorCustom(:theme="theme")
  HorizontalViewAW(
    :theme="theme"
    :appear="appear"
    :scrollbar="scrollbar"
    )
    Operating
</template>

<script>
import CursorCustom from "../../components/elements/Cursor/index";
import HorizontalViewAW from "../../views/HorizontalViewAW/index";
import Operating from "../../components/blocks/Operating/index";
export default {
  name: "PageOperating",
  data() {
    return {
      scrollbar: true,
      type: "full",
      theme: "light",
      scrollbar: true,
      appear: "normal",
      animation: "clear"
    };
  },
  methods: {
    clearCursor() {
      document.querySelector(".circle-cursor--outer").remove();
      document.querySelector(".circle-cursor--inner").remove();
    }
  },
  beforeDestroy() {
    document.querySelector("canvas").remove();
    this.clearCursor();
  },
  components: {
    HorizontalViewAW,
    CursorCustom,
    Operating
  }
};
</script>

