    showPage() {
      this.$el.classList.add("active");
      setTimeout(() => {
        this.$el.querySelector(".operating__inner").classList.add("clean");
      }, 2000);
    },
    clearCursor() {
      document.querySelector(".circle-cursor--outer").remove();
      document.querySelector(".circle-cursor--inner").remove();
    },
    onScroll(e) {
      const scroll = this.scroll + -1 * e.deltaY;
      this.scroll = Math.min(
        Math.max(scroll, 0),
        this.$refs.container.scrollHeight - window.innerHeight
      );
    },
    setup() {
      loop.start();
      this.vs = new VirtualScroll({
        mouseMultiplier: 0.7,
        touchMultiplier: 0.7,
        passive: true
      });
      loop.add(this.checkSmooth, "checkSmooth");
      inobounce.enable();
      this.vs.on(this.onScroll);
    },
    checkSmooth() {
      if (Math.round(this.scroll) !== Math.round(this.translate)) {
        this.translate = roundDec(lerp(this.translate, this.scroll, 0.15));
        if (this.scroll > 100) {
          this.$el
            .querySelector(".operating__head")
            .classList.add("operating__head--hidden");
        } else {
          this.$el
            .querySelector(".operating__head")
            .classList.remove("operating__head--hidden");
        }
        if (this.scroll > 450) {
          this.$el.querySelector(".arrows").classList.add("arrows--active");
        } else {
          this.$el.querySelector(".arrows").classList.remove("arrows--active");
        }
      }
    }
  },
  mounted() {
    this.showPage();
    this.setup();
    this.scrollHeight = this.$refs.container.scrollHeight;
  },
  beforeDestroy() {
    loop.remove("checkSmooth");
    this.vs.off(this.onScroll);
    loop.stop(true);
  },
