<style lang="sass">
@import "src/sass/general/medias"
@import "src/sass/general/variables"
@import "src/sass/general/text"
.operating
  width: 100vw
  min-height: 100vh
  &__controls
    position: fixed
    top: 0
    right: $view-indent
    width: 80px
    display: flex
    flex-direction: column
    justify-content: flex-end
    transition-delay: 2s
    transition-timing-function: cubic-bezier(0.77, 0, 0.175, 1)
    transition-duration: 2s
    opacity: 0
    height: 100vh
    z-index: 2
    border-left: 1px solid $grey3
    @media (max-width: $sm-desktop)
      width: 60px
    .arrows
      height: 50vh
      transition-duration: .6s
      display: flex
      position: relative
      flex-direction: column
      &--active
        height: 100vh
      .arrow
        display: flex
        background: $light
        overflow: hidden
        transition-duration: .4s
        border-top: 1px solid $grey3
        +center-flex
        position: relative
        height: 50%
        img
          transition-duration: inherit
          z-index: 1
        &:before
          content: ''
          transition-timing-function: cubic-bezier(.77,0,.175,1)
          transition-duration: inherit
          position: absolute
          width: 100%
          left: 0
          z-index: 0
          bottom: 0
          transform: translateX(100%)
          background: $beige
          height: 100%
        &:not(.swiper-button-disabled):hover
          &:before
            transform: translateX(0%)
      .prev
        img
          transform: scale(-1, 1)
  &__back
    width: 100%
    min-height: 100vh
    position: fixed
    top: 0
    left: 0
    z-index: -1
    .btn_start
      display: none
  &__inner
    z-index: 0
    margin-top: 100vh
    background: $light
    width: 100%
    padding: 60px 14vw
    min-height: 151vh
    transition-delay: .8s
    transition-timing-function: cubic-bezier(0.77, 0, 0.175, 1)
    transition-duration: 2s
    @media (max-width: $m-desktop)
      padding: 40px 10vw 40px 9vw
  &__head
    width: 100%
    height: 100vh
    padding: 0 14vw
    position: absolute
    background: rgba(0,0,0,.3)
    top: 0
    color: $light
    padding-top: 30vh
    display: flex
    justify-content: space-between
    @media (max-width: $sm-desktop)
      flex-direction: column
      justify-content: flex-start
    .head__part
      &:first-child
        width: 60%
        @media (max-width: $m-desktop)
          width: 50%
        @media (max-width: $sm-desktop)
          width: 80%
      &:last-child
        width: 40%
        @media (max-width: $m-desktop)
          width: 50%
        @media (max-width: $sm-desktop)
          width: 80%
    .title
      font-family: 'Baza'
      text-transform: uppercase
      letter-spacing: 3px
      font-weight: 300
      transition-duration: .7s
      transform-style: preserve-3d
      transition-timing-function: cubic-bezier(.165,.84,.44,1)
      transform: perspective(1000px) rotate3d(0,0,0,40deg)
      margin: 0
      font-size: 48px
      overflow: hidden
      span
        transition-duration: .5s
        transform: translateY(100%)
        display: inline-block
        transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1)
    p
      font-family: 'Futura PT Book'
      font-size: 16px
      opacity: .7 
      letter-spacing: .4px
      transition-duration: .7s
      transform-style: preserve-3d
      transition-timing-function: cubic-bezier(.165,.84,.44,1)
      transform: perspective(1000px) rotate3d(0,0,0,40deg)
      span
        transition-duration: .6s
        transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1)
        transform: translateY(20px)
        opacity: 0
        display: inline-block
    &--hidden
      p
        transform: perspective(1000px) rotate3d(20,0,0,70deg) translateY(-80px)
        opacity: 0
      .title
        transform: perspective(1000px) rotate3d(20,0,0,70deg) translateY(-80px)
        opacity: 0
    @media (max-width: $m-desktop)
      padding: 30vh 9vw 0
    @media (max-width: $sm-desktop)
      padding-top: 19vh
  .swiper-button-disabled
    img
      opacity: .5
.operating__inner.clean
  transition-delay: 0s
  transition-duration: 0s
.operating.active
  .operating__controls
