    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    background: #1D1A1B
.layers
  position: fixed
  width: 100vw
  height: 100vh
  z-index: 1
  top: 0
  left: 0
  transition-duration: .3s
  .layer
    width: inherit
    height: inherit
    position: absolute
    will-change: transform
    top: 0
    background-size: 100%
    display: none
    left: 0
    background-repeate: no-repeate
    @media (max-width: $m-desktop)
      background-position: center
      background-size: 110%
    @media (max-width: $sm-desktop)
      background-size: 125%
