          z-index: 1
          width: 28vw
          @media (max-width: $b-tablet)
            width: 100%
            margin: 0
            height: 80vh
          &:before
            left: initial
            right: -30px
        &:nth-child(2)
          width: 20vw
          height: 25vh
          right: -25%
          z-index: 0
          filter: brightness(70%)
          bottom: 10vh
          @media (max-width: $b-tablet)
            display: none
  &-5
    width: 120vw!important
    padding-right: 0!important
    @media (max-width: $l-desktop)
      width: 130vw!important
    @media (max-width: $sm-desktop)
      width: 140vw !important
    @media (max-width: $s-desktop)
      width: 160vw !important
    .content__part
      padding-right: 7vw
    
    .layout
      display: none
      @media (max-width: $b-tablet)
        margin-top: 80px
        display: block
        a
          color: $gold
          text-transform: uppercase
          font-family: 'Baza'
          width: 100vw
          letter-spacing: 2px
          display: flex
          align-items: center
          font-size: 40px
          justify-content: space-between
        &__arrow
          height: 1px
          content: ''
          position: relative
          width: 45px
          background: $brown2
          &:before
            width: 6px
            height: 6px
            display: block
            position: absolute
            top: 0
            bottom: 0
            content: ''
            margin: auto
            border-top: 1px solid $brown2
            border-right: 1px solid $brown2
          &:first-child:before
            right: 0
            transform: rotate(45deg)
          &:last-child:before
            left: 0
            transform: rotate(-135deg)
    .border-effect-trigger
      position: absolute
      right: 10vw
      height: 100%
.decorate
  position: absolute
  height: 100vh
  width: 50%
  top: 0
  z-index: -1!important
  left: 0
  right: 0
  margin: auto
  background-size: cover
  background-image: url(./pics/part-3/decorate.svg)
  @media (max-width: $b-tablet)
    height: auto

$cell: 180px
$cell-m: 150px
$cell-s: 140px
.cells
  display: flex
  z-index: 2
  position: relative
  @media (max-width: $b-tablet)
    width: max-content
  @media (max-width: $s-tablet)
    flex-wrap: wrap
    width: calc(100vw - 45px)
    padding-right: 45px
  @media (max-width: $b-mobile)
    padding-right: 0
  .line-middle
    margin-top: calc(#{$cell} - 0px)
    position: absolute
    background: $brown2
    height: 1px
    width: 100%
    transform: scaleX(0) translateZ(0)
    //transition-delay: .2s
    transition-duration: .4s
    transition-timing-function: cubic-bezier(.165,.84,.44,1)
    transform-origin: right
    @media (max-width: $m-desktop)
      margin-top: calc(#{$cell-m} - 0px)
    @media (max-width: $s-desktop)
      margin-top: calc(#{$cell-s} - 0px)
  .cell
    width: $cell
    height: $cell
    display: flex
    +center-flex
    position: relative
    text-align: center
    flex-direction: column
    padding: 0 20px
    @media (max-width: $m-desktop)
      width: $cell-m
      height: $cell-m
    @media (max-width: $s-desktop)
      width: $cell-s
      height: $cell-s
    @media (max-width: $s-tablet)
      width: 50%
      border: 1px solid $brown2
    &-1
      .cell__inner
        .line-3
          transition-delay: .4s
        .line-2
          height: 200%
          transition-duration: .2s
          transition-delay: .6s
        .line-1
          transition-delay: .5s
    &-2
      .cell__inner
        .line-2
          height: 200%
          bottom: 0
          top: initial
          transition-duration: .2s
          transition-delay: .8s
          transform-origin: bottom 
        .line-3
          display: none
        .line-4
          transition-delay: .7s
    &-3
      .cell__inner
        .line-3
          display: none
        .line-1
          transition-delay: .9s
        .line-2
          height: 200%
          transition-delay: .8s
    &-4
      .cell__inner
        .line-3
          display: none
        .line-4
          transition-delay: 1.2s
        .line-2
          bottom: 0
          transform-origin: bottom
          top: initial
          transition-duration: .2s
          height: 200%
          transition-delay: 1.3s
    &-5
      
      .cell__inner
        .line-3
          display: none
        .line-1
          transition-delay: 1.5s
        .line-2
          transition-delay: 1.6s
    &:nth-child(even)
        .cell__inner
          span
            &:nth-child(4)
              display: none
    &__inner
      position: absolute
      width: 100%
      height: 100%
      +center
      @media (max-width: $s-tablet)
        display: none
      .line
        position: absolute
        background: $brown2
        transition-duration: .1s
        transition-timing-function: linear
        &-1
          top: 0
          width: 100%
          height: 1px
          transform: scaleX(0) translateZ(0)
          transform-origin: left
          left: 0
        &-2
          top: 0
          width: 1px
          height: 100%
          right: 0
          transform-origin: top
          transform: scaleY(0) translateZ(0)
        &-3
          bottom: 0
          width: 1px
          height: 100%
          left: 0
          transform: scaleY(0) translateZ(0)
          transform-origin: bottom
        &-4
          bottom: 0
          width: 100%
          height: 1px
          left: 0
          transform-origin: left
          transform: scaleX(0) translateZ(0)
    &:nth-child(odd)
      margin-top: calc(#{$cell} - 0px)
      margin-left: -1px
      margin-right: -1px
      @media (max-width: $m-desktop)
        margin-top: calc(#{$cell-m} - 0px)
      @media (max-width: $s-desktop)
        margin-top: calc(#{$cell-s} - 0px)
      @media (max-width: $s-tablet)
        margin: 0 -2px -2px calc(50% -1px)
      .cell__inner
        .line
          &-1
            display: none
    .count
      color: $gold
      font-family: 'Montserrat', sans-serif
      font-size: 54px
      font-weight: 100
      overflow: hidden
      @media (max-width: $b-tablet)
        font-size: 48px
      .ct
        transition-duration: .7s
        display: inline-block
        transform: translateY(105%) translateZ(0)
        transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1)
        @media (max-width: $b-tablet)
          transition-delay: 0s!important
        &:nth-child(1)
          transition-delay: .7s
        &:nth-child(2)
          transition-delay: .75s
        &:nth-child(3)
          transition-delay: .8s
    .text
      font-family: 'Futura PT Book'
      color: rgba(255,255,255,.6)
      font-size: 13px
      transition-delay: 1s
      opacity: 0
      transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1)
      transition-duration: .7s
      @media (max-width: $b-tablet)
        transition-delay: .4s!important

.cells.animated
  .line-middle
    @media (max-width: $s-tablet)
      display: none
    transform: scaleX(1) translateZ(0)
  .cell
    .cell__inner
      .line-1
        transform: scaleX(1) translateZ(0)
      .line-2
        transform: scaleY(1) translateZ(0)
      .line-3
        transform: scaleY(1) translateZ(0)
      .line-4
        transform: scaleX(1) translateZ(0)
.cell.animated
  .text
    opacity: 1
  .count
    .ct
      transform: translateY(0) translateZ(0)
.page
  transform: none!important
.description.animated
  .count
    @media (max-width: $b-tablet)
      transform: translateY(0)!important
      opacity: 1!important
      transition-delay: .1s
    .link
      opacity: 1!important
  .description__part
    @media (max-width: $b-tablet)
      transform: translateY(0)!important
      opacity: 1!important
      transition-delay: .2s
.title.animated
  @media (max-width: $b-tablet)
    opacity: 1!important
    transform: translateY(0)!important
    transition-delay: .2s
.pic__item.animated
  @media (max-width: $b-tablet)
    transform: translateY(0px)!important
    opacity: 1!important
</style>
